<template>
  <div>
     <pre>{{ user }}</pre>
  </div>
</template>

<script>
import { fetchUserProfile } from '../services/getUser'
export default {
        name: 'profile' , 
        data() {
            return {
                email : this.$store.state.auth.email, 
                user : []
            }
        } , 
        created() {
            fetchUserProfile(this.email)
                         .then(User=>{this.user = User.data ;})

        }
}
</script>

